/* eslint-disable */
import React, { Component } from 'react'

import './result.less'

import { Row, Col } from 'antd';
import 'antd/dist/antd.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCode, faUserPlus, faUsers, faLocationArrow } from '@fortawesome/free-solid-svg-icons'

class result extends Component {
    constructor(props){
        super(props)
        this.state={
            rankP1:'',
            rankP2:''
        }
    }

    render() {
        const { player1 } = JSON.parse(sessionStorage.getItem('p1'))
        const { player2 } = JSON.parse(sessionStorage.getItem('p2'))
        return (
          <div className='result'>
            <Row justify="space-around">
              <Col xs={{ span: 10 }} lg={{ span: 4 }}>
                <figure>
                  <h3>{this.state.rankP1}</h3>
                  <img src={player1.avatar_url} />
                  <figcaption className='scores'>Scores:{player1.public_repos}</figcaption>
                  <figcaption className='itemName'>{player1.name}</figcaption>
                  <figcaption>
                    <ul className="icon-ul">
                      <li><FontAwesomeIcon icon={faLocationArrow} /></li>
                      <li><FontAwesomeIcon icon={faUsers} /></li>
                      <li><FontAwesomeIcon icon={faUserPlus} /></li>
                      <li><FontAwesomeIcon icon={faCode} /></li>
                    </ul>
                    <ul className="msg-ul">
                      <li><span>{player1.location}</span></li>
                      <li><span>{player1.followers}</span></li>
                      <li><span>{player1.following}</span></li>
                      <li><span>{player1.public_repos}</span></li>
                    </ul>
                  </figcaption>
                </figure>
              </Col>
              <Col xs={{ span: 10 }} lg={{ span: 4 }}>
                <figure>
                  <h3>{this.state.rankP2}</h3>
                  <img src={player2.avatar_url} />
                  <figcaption className='scores'>Scores:{player2.public_repos}</figcaption>
                  <figcaption className='itemName'>{player2.name}</figcaption>
                  <figcaption>
                    <ul className="icon-ul">
                      <li><FontAwesomeIcon icon={faLocationArrow} /></li>
                      <li><FontAwesomeIcon icon={faUsers} /></li>
                      <li><FontAwesomeIcon icon={faUserPlus} /></li>
                      <li><FontAwesomeIcon icon={faCode} /></li>
                    </ul>
                    <ul className="msg-ul">
                      <li><span>{player2.location}</span></li>
                      <li><span>{player2.followers}</span></li>
                      <li><span>{player2.following}</span></li>
                      <li><span>{player2.public_repos}</span></li>
                    </ul>
                  </figcaption>
                </figure>
              </Col>
            </Row>
            <Row>
              <Col span={12} offset={6} className='reset-btn'>
                <button onClick={()=>{
                            this.props.history.go(-1)
                        }}
                >RESET
                </button>
              </Col>
            </Row>
          </div>
        )
    }

    componentDidMount(){
        const { player1 } = JSON.parse(sessionStorage.getItem('p1'))
        const { player2 } = JSON.parse(sessionStorage.getItem('p2'))
        if(player1.public_repos>player2.public_repos){
            this.setState({
                rankP1:'Winner',
                rankP2:'Loser'
            })
        }else if(player1.public_repos < player2.public_repos){
            this.setState({
                rankP1:'Loser',
                rankP2:'Winner'
            })
        }else{
            
        }
    }
}

export default result
